
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;

  .navigation-logo {

  }

  .navigation-navbar {
    display: flex;
    text-align: center;
    a {
      color: inherit;
      text-decoration: none;
    }

    & > div {
      padding-right: 50px;
      width: fit-content;
    }

    .material-icons-outlined {
      font-size: 24px;
      display: inline-block;
      color: blue;

      &.selected {
        color: red;
      }
    }
  }
}