
.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding: 25px 30px;

  .wip-warning {
    position: absolute;
    cursor: pointer;
    top: 25px;
    right: 20px;
    color: white;
    background: #F9812A;
    padding: 5px;
  }

  .home-body-start {
  }

  .home-text {
    .home-text-intro {
      font-size: 45px;
      line-height: 53px;
      font-family: Helvetica, sans-serif, Arial;
    }

    .home-text-subheading {
      color: #8d8d8d;
      font-size: 11px;
      letter-spacing: 3px;
    }
  }

  .home-body-end {}
}